<script lang="ts">
import {defineComponent} from 'vue'
import NodeTree from "@/components/node/nodeTree.vue";

export default defineComponent({
  name: "NodeView",
  components: {NodeTree},
  props: ['place'],
  data() {
    return {
      object: {}
    }
  },
  methods: {
    async init() {
      if (this.place)
        this.object = await this.$yin.get(this.place)
    }
  },
  mounted() {
    this.init()
  }
})
</script>

<template>
  <div class="nodeView">
    <node-tree :object="object" v-if="object._id"/>
  </div>
</template>

